<template>
  <div class="page page-with-padding">
    <wv-button @click="ticketPickerShow = true"></wv-button>
    <wv-button @click="dayPickerShow = true"></wv-button>

    <wv-picker v-model="ticketPickerShow" :slots="ticketSlots" @change="onChange"></wv-picker>
    <wv-picker v-model="dayPickerShow" :slots="daySlots" @change="onChange"></wv-picker>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        ticketValue: [],
        dayValue: [],
        ticketPickerShow: false,
        dayPickerShow: false,
        ticketSlots: [
          {
            values: [
              '',
              '',
              '',
              '',
              ''
            ],
            defaultIndex: 2
          }
        ],
        daySlots: [
          {
            values: [
              '',
              '',
              '',
              '',
              ''
            ],
            defaultIndex: 0
          },
          {
            values: [
              '',
              ''
            ],
            defaultIndex: 0
          }
        ]
      }
    },

    methods: {
      onChange (picker, value) {
        this.$nextTick(() => {
          console.log(picker.getValues())
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  .page {
    background-color: #fff;
  }
</style>



