<template>
  <div class="page-with-padding">
    <wv-button type="primary" @click="showTips"> (5S)</wv-button>
  </div>
</template>

<script>
  import TopTips from '../../src/components/top-tips'

  export default {
    methods: {
      showTips () {
        TopTips({
          message: '',
          duration: 5000
        })
      }
    }
  }
</script>



