import icon from '../icon/index'

export default {
  components: {
    [icon.name]: icon
  },

  name: 'wv-input',

  props: {
    type: {
      type: String,
      default: 'text'
    },
    label: String,
    labelWidth: {
      type: Number,
      default: 105
    },
    placeholder: String,
    value: String,
    readonly: Boolean,
    disabled: Boolean,
    required: {
      type: Boolean,
      default: false
    },
    pattern: String,
    novalidate: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      active: false,
      currentValue: this.value
    }
  },

  computed: {
    // 
    valid () {
      if (this.pattern) {
        const reg = new RegExp(this.pattern)
        if (!reg.test(this.currentValue)) return false
      }

      if (this.required && this.currentValue === '') return false

      return true
    }
  },

  methods: {
    doCloseActive () {
      this.active = false
    },

    handleInput (event) {
      this.currentValue = event.target.value
    },

    handleClear () {
      if (this.disabled || this.readonly) return
      this.currentValue = ''
    }
  },

  watch: {
    currentValue (val) {
      this.$emit('input', val)
    },

    value (val) {
      this.currentValue = val
    }
  }
}



