<template>
  <div class="page-with-padding">
    <wv-button type="default" @click="showSuccess"></wv-button>
    <wv-button type="default" @click="showError"></wv-button>
    <wv-button type="default" @click="showText"></wv-button>
  </div>
</template>

<script>
  import Toast from '../../src/components/toast'

  export default {
    methods: {
      showSuccess () {
        Toast('')
      },

      showError () {
        Toast({
          duration: 1000,
          message: '',
          icon: 'warn'
        })
      },

      showText () {
        Toast({
          duration: 1000,
          message: 'hello',
          type: 'text'
        })
      }
    }
  }
</script>



